import React from "react";

function OtherInfo({ formData, setFormData }) {
  return (
    <div className="other-info-container">
      <label>Grade</label>
      <input
        type="text"
        placeholder="Grade..."
        value={formData.grade}
        onChange={(e) => {
          setFormData({ ...formData, grade: e.target.value });
        }}
      />
      <label>Fonction</label>
      <input
        type="text"
        placeholder="Fonction..."
        value={formData.fonction}
        onChange={(e) => {
          setFormData({ ...formData, fonction: e.target.value });
        }}
      />
      <label>Lieu d'exercice</label>
      <input
        type="text"
        placeholder="Lieu ..."
        value={formData.lieu}
        onChange={(e) => {
          setFormData({ ...formData, lieu: e.target.value });
        }}
      />
      <label>Adresse</label>
      <input
        type="text"
        placeholder="Adresse..."
        value={formData.adressefonc}
        onChange={(e) => {
          setFormData({ ...formData, adressefonc: e.target.value });
        }}
      />
      <label>Tlphone</label>
      <input
        type="tel"
        placeholder="Tlphone..."
        value={formData.telfonc}
        onChange={(e) => {
          setFormData({ ...formData, telfonc: e.target.value });
        }}
      />
      <label>Fax</label>
      <input
        type="text"
        placeholder="Fax..."
        value={formData.fax}
        onChange={(e) => {
          setFormData({ ...formData, fax: e.target.value });
        }}
      />
    </div>
  );
}

export default OtherInfo;