import React, { useState } from "react";
import SignUpInfo from "./SignUpInfo";
import PersonalInfo from "./PersonalInfo";
import OtherInfo from "./OtherInfo";
import Formation from "./Formation";
import { Link } from 'react-router-dom';

function Form() {
  const [page, setPage] = useState(0);
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    confirmPassword: "",
    firstName: "",
    lastName: "",
    pseudo: "",
    nationality: "",
    datenai: "",
    adresse:"",
    situation:"",
    tlphone:"",
    wifename:"",
    nbrenfant:"",
    nmrcompte:"",
    grade:"",
    fonction:"",
    lieu:"",
    adressefonc:"",
    telfonc:"",
    fax:"",
    diplome:"",
    stage:"",
    publications:"",
    langue:"",
    projet:""
  });
  const FormTitles = ["Sign Up", "Personal Info", "Other","Formation"];
  const PageDisplay = () => {
    if (page == 0) {
      return <SignUpInfo formData={formData} setFormData={setFormData} />;
    } else if (page == 1) {
      return <PersonalInfo formData={formData} setFormData={setFormData} />;
    } else if(page == 2) {
      return <OtherInfo formData={formData} setFormData={setFormData} />;
    } else {
      return <Formation formData={formData} setFormData={setFormData} />;
    } };
  return (
    <div className="form">
    <div className="progressbar">
        <div style={{ width: page == 0 ? "25%" : page == 1 ? "50%" :page==2 ? "75%": "100%" }} ></div> 
    </div>
    <div className="form-container">
      <div className="header">
        <h1>{FormTitles[page]}</h1>
      </div>
      <div className="body">{PageDisplay()}</div>
      <div className="footer">
        <button disabled={page == 0} onClick={() => { setPage((currPage) => currPage - 1); }} >Prev</button>
        <button onClick={() => { if (page == FormTitles.length - 1) { 
             alert("FORM SUBMITTED"); console.log(formData);
             } else {
              setPage((currPage) => currPage + 1);
            }
          }}
        >
          {page == FormTitles.length - 1 ? "Submit" : "Next"}
         
        </button>
        <Link to='/login'>Login</Link>
      </div>
    </div>
  </div>
  );
}

export default Form;