import React from "react";

function Formation({ formData, setFormData }) {
  return (
    <div className="Formation-container">
      <label>Grade</label>
      <input
        type="text"
        placeholder="Grade..."
        value={formData.nationality}
        onChange={(e) => {
          setFormData({ ...formData, nationality: e.target.value });
        }}
      />
      <label>Diplome universitaire</label>
      <input
        type="text"
        placeholder="Diplome..."
        value={formData.diplome}
        onChange={(e) => {
          setFormData({ ...formData, diplome: e.target.value });
        }}
      />
      <label>Stage</label>
      <input
        type="text"
        placeholder="Stage..."
        value={formData.stage}
        onChange={(e) => {
          setFormData({ ...formData, stage: e.target.value });
        }}
      />
      <label>Publications</label>
      <input
        type="text"
        placeholder="Publications..."
        value={formData.publications}
        onChange={(e) => {
          setFormData({ ...formData, publications: e.target.value });
        }}
      />
      <label>Langue</label>
      <input
        type="text"
        placeholder="Langues..."
        value={formData.langue}
        onChange={(e) => {
          setFormData({ ...formData, langue: e.target.value });
        }}
      />
      <label>Projets</label>
      <input
        type="text"
        placeholder="Projects..."
        value={formData.projet}
        onChange={(e) => {
          setFormData({ ...formData, projet: e.target.value });
        }}
      />
    </div>
  );
}

export default Formation;